// eslint-disable-next-line no-var
var errorMsgs = {
  emptyField: {
    en: 'This field cannot be empty',
    fr: 'Ce champ doit être rempli',
  },
  emailInvalid: {
    en: 'Invalid email address',
    fr: 'Adresse courriel invalide',
  },
  upcCodeInvalid: {
    en: 'Please enter a valid UPC code',
    fr: 'Veuillez entrer un code cup valide',
  },
  incorrectFormat: {
    en: 'Incorrect format',
    fr: 'Format incorrect',
  },
  defaultError: {
    en: 'Invalid input',
    fr: 'Entrée invalide',
  },
};

function getLabelForField(field) {
  return field.parentNode.querySelector('.form-control-placeholder');
}

function applyLabelCss(field) {
  const fieldLabel = getLabelForField(field);
  if (fieldLabel) {
    // TODO: investigate if possible to use classList/add/remove class
    fieldLabel.style.cssText = "font-size: 75%; transform: translate3d(1px, -40%, 0);";
  }
}

export function clearLabelCss(field) {
  const fieldLabel = getLabelForField(field);
  if (fieldLabel) {
    // TODO: investigate if possible to use classList/add/remove class
    fieldLabel.style.cssText = '';
  }
}

function hasError(field) {
  const { validity } = field;

  if (validity.valid) {
    applyLabelCss(field);
    return false;
  }

  // get document lang
  let documentLang = document.documentElement.lang;
  if (!documentLang) {
    console.warn('No language is set on document, using "en" as default.');
    documentLang = 'en';
  }

  // create output var
  let error = undefined;

  if (validity.valueMissing) {
    clearLabelCss(field);
    error = errorMsgs.emptyField[documentLang];
  }

  if (validity.typeMismatch && field.type === 'email') {
    applyLabelCss(field);
    error = errorMsgs.emailInvalid[documentLang];
  }

  if (validity.patternMismatch) {
    if (field.hasAttribute('title')) {
      applyLabelCss(field);
      error = field.getAttribute('title');
    } else {
      error = errorMsgs.incorrectFormat[documentLang];
    }
  }

  if (validity.customError) {
    // upc code is the only custom error
    applyLabelCss(field);
    error = errorMsgs.upcCodeInvalid[documentLang];
  }

  // set default value if no value
  error = error || errorMsgs.defaultError[documentLang];

  return error;
}

function showError(field, error) {
  field.classList.add('error');

  // get field id
  const id = field.id || field.name;
  if (!id) return;

  field.setAttribute('aria-describedby', `error-for-${id}`);

  // checkbox, dont show error message
  if (field.type === 'checkbox') {
    return;
  }

  // get field error message
  let message = field.form.querySelector(`.error-message#error-for-${id}`);
  if (!message) {
    message = document.createElement('div');
    message.className = 'error-message';
    message.id = `error-for-${id}`;
    field.parentNode.appendChild(message);
  }

  // set error message to display
  message.innerHTML = error;

  // set styles to display error message
  message.classList.add('visible');
}

function removeError(field) {
  field.classList.remove('error');

  field.removeAttribute('aria-describedby');

  const id = field.id || field.name;
  if (!id) return;

  const message = field.form.querySelector(`.error-message#error-for-${id}`);
  if (!message) return;

  message.innerHTML = '';
  message.classList.remove('visible');
}

function formValidation(event) {
  const { target } = event;

  if (target.form && target.form.classList.contains('validate')) {
    const error = hasError(target);
    if (error) {
      showError(target, error);
    } else {
      removeError(target);
    }
  }
}

export default formValidation;
