function revealUPCField() {
  const maxFields = 4;

  // get data from html
  let form = document.getElementById('contestForm');
  let numVisibleFields = parseInt(form.dataset.numUpcFieldsVisible, 10);

  if (numVisibleFields > maxFields) {
    return;
  }

  // increase html data
  numVisibleFields++;
  form.dataset.numUpcFieldsVisible = numVisibleFields;

  switch (numVisibleFields) {
    case 2:
      // show hidden 2nd field
      let hiddenUPC1 = document.getElementById('hiddenUPC1');
      hiddenUPC1.classList.add('visible');
      break;

    case 3:
      // show hidden 3rd field
      let hiddenUPC2 = document.getElementById('hiddenUPC2');
      hiddenUPC2.classList.add('visible');
      break;

    case 4:
      // show hidden 4th field, hide button
      let hiddenUPC3 = document.getElementById('hiddenUPC3');
      let addUpcCodeContainer = document.getElementById('addUpcCodeContainer');
      hiddenUPC3.classList.add('visible');
      addUpcCodeContainer.classList.add('hidden');
      break;
  }
}
export default revealUPCField;
