function calculateUpcSum(barcode) {
  return barcode.split('').reduce((sum, c, idx) => {
    const multiplier = idx % 2 ? 1 : 3;
    return sum + multiplier * Number(c);
  }, 0);
}

function isValidBarcode(barcode) {
  return barcode.length === 12 && calculateUpcSum(barcode) % 10 === 0;
}

function validateBarcode(event) {
  const element = event.target;
  const barcode = element.value;

  return isValidBarcode(barcode)
    ? element.setCustomValidity('')
    : element.setCustomValidity('Please enter a valid UPC code');
}

export default validateBarcode;
