import { submitForm } from './js/show-confirmation';
import acceptOnlyNumbers from './js/accept-only-numbers';
import formValidation from './js/form-validation';
import revealUPCField from './js/reveal-upc-field';
import smoothScroll from './js/smooth-scroll';
import validateBarcode from './js/validate-barcode';


document.addEventListener('DOMContentLoaded', () => {
  // init bootstrap tooltip
  $('[data-toggle="tooltip"]').tooltip({
    animated: 'fade',
    placement: 'bottom',
    html: true,
  });

  // applies smooth scrolling to all anchors
  $('a[href*=\\#]').on('click', smoothScroll);

  // dom elements
  const addUpcBtn = document.getElementById('addUpcCode');
  const form = document.getElementById('contestForm');
  const upcInputs = document.getElementsByClassName('upc-code-input');

  // add event handlers
  if (addUpcBtn) {
    addUpcBtn.onclick = revealUPCField;
  }

  let numInputs = upcInputs.length;
  for (let i = 0; i < numInputs; i++) {
    let input = upcInputs[i];
    input.onchange = validateBarcode;
    input.onkeyup = acceptOnlyNumbers;
  }

  form.onsubmit = submitForm;

  document.addEventListener('blur', formValidation, true);
});
