function showConfirmation() {
  $('[data-toggle]').each(function () {
    $(this).toggleClass('d-none');
  });
  $('.hero-banner').toggleClass('smaller');
  $('[data-confirmation]').each(function () {
    $(this).removeClass('d-none');
  });

  window.scrollTo(0, 0);
}

export function submitForm(event) {
  event.preventDefault();

  let response = grecaptcha.getResponse();
  let captcha = document.getElementById('gRecaptcha');

  if (!response) {
    captcha.scrollIntoView({ block: 'center' });
    $('.captcha-error').addClass('visible');
    $('.g-recaptcha').addClass('highlight');
    return;

  } else {
    $('.captcha-error').removeClass('visible');
    $('.g-recaptcha').removeClass('highlight');

    const $form = $(this);
    const serializedData = $form.serialize() + '&' + $.param({ 'referrer': document.URL });

    $.post(
      $form.attr('action'),
      serializedData
    )
      .done(showConfirmation);
  }
}
